import item from './goodsList--Item';
import PopUp from '../../../components/my-components/PopUp.vue';
export default {
    components: {
        item,
        PopUp
    },
    data() {
        return {
            menuItem: {},
            goodData: {},
            staticUrl: '/static/img/global/tmpAct.png',
            show: false,
            show2: false
        }
    },
    methods: {
        closeAll() {
            this.show = false;
            this.show2 = false;
        },
        clickItem(val) {
            if (val.type == 'menu-item') {
                // this.show = true;
                if (val.name == '编辑') {
                    this.$emit('click', val);
                } else if (val.name == '下架') {} else if (val.name == '删除') {} else if (val.name == '推广商品') {
                    this.show2 = true;
                }
            }
        },
        showBanner: function() {
            this.show = true;
        },
        closeBanner: function() {
            this.show = false;
        },
    },
}
